import React, { useState } from 'react';
import { Col } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faStar } from "@fortawesome/free-solid-svg-icons";
import { addProduct } from "../../redux/action/userAction"
import { Link } from "react-router-dom"
import bor from "../../assets/images/nwrd.gif"
import bor2 from "../../assets/images/hr.png"


function ProductComp(props) {

    const dispatch = useDispatch()

    const selector = useSelector(state => state.addCart)


    function handleClick(index) {
        const addedItem = selector.find(c => c.id === index.id)
        if (addedItem) {
            addedItem.quantity += 1
        } else {
            dispatch(addProduct(props.index))
        }
    }

    const [hearth, setHearth] = useState(false)

    const dart = {
        backgroundImage: hearth ? `url(${bor})` : `url(${bor2})`,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center center",
        backgroundSize: "cover",
        cursor: "pointer",
        width: "30px",
        height: "30px",
    }

    function handleHeart() {
        setHearth(!hearth)
    }

    return (
        <Col className="mb-pr mt-5" md={3} sm={12}>
            <div className="product">
                <Link to={{
                    pathname: `/preview/id:${props.index.id}`,
                    details: props.index,
                }}>
                    <div>
                        <img className="w-100" src={props.index.url} alt="product" />
                    </div>
                </Link>
                <div className="text pt-3 px-3">
                    <h3>
                        <span>{props.index.dressName}</span>
                    </h3>
                    <div className="d-flex justify-content-between">
                        <div className="pricing">
                            <p className="price"><span>{props.index.price}$</span></p>
                        </div>
                        <div className="rating">
                            <p className="text-right">
                                <FontAwesomeIcon style={{ color: "yellow" }} icon={faStar} />
                                <FontAwesomeIcon style={{ color: "yellow" }} icon={faStar} />
                                <FontAwesomeIcon style={{ color: "yellow" }} icon={faStar} />
                                <FontAwesomeIcon style={{ color: "yellow" }} icon={faStar} />
                                <FontAwesomeIcon style={{ color: "yellow" }} icon={faStar} />
                            </p>
                        </div>
                    </div>
                    <hr />

                    <div className="d-flex justify-content-between">
                        <button className="custom-btn" onClick={() => handleClick(props.index)} style={{ textDecoration: "none", color: "black" }} href="/">
                            ADD TO CART +
                        </button>
                        <div onClick={handleHeart} style={dart}></div>
                    </div>
                </div>
            </div>

        </Col>
    )
}

export default ProductComp



