import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import { Switch, Route, Link, useRouteMatch, useParams } from "react-router-dom"
import AddPost from './Panel/AddPost';

import PostList from './Panel/PostList';
import EditPost from "./Panel/EditPost"

const Panel = () => {
    let { path, url } = useRouteMatch();

    return (
        <div style={{ paddingTop: "200px" }}>
            <Container>
                <Row>
                    <Col md={2}>
                        <ul className="list-unstyled panel-tab">

                            <li><Link className="nav-link" to={`${url}/posts`}>Posts</Link></li>
                            <li>
                                <Link className="nav-link" to={`${url}/addpost`}>Add Post</Link>
                            </li>
                            {/* <li>
                                <Link className="nav-link" to={`${url}/editpost`}>Edit Post</Link>
                            </li> */}
                        </ul>
                    </Col>
                    <Col md={8}>
                        <Switch>
                            <Route exact path={path}>
                                <PostList />
                            </Route>
                            <Route path={`${path}/:topicId`} >
                                <System exact />
                            </Route>
                        </Switch>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}


function System() {

    let { topicId } = useParams();


    if (topicId === "addpost") {
        return (<AddPost exact />)
    }

    if (topicId === "posts") {
        return (<PostList />)
    }
    if (topicId === "editpost") {
        return (<EditPost />)
    }

}

export default Panel;