import React, { useEffect } from 'react'
import { Container, Row, Col } from 'reactstrap'
import pusth from "../assets/images/loading.gif"
import { useSelector } from 'react-redux'


function Preview() {

    const currentUrl = window.location.pathname.split("/preview/id:")[1]
    const currentProduct = useSelector(state => state.cartReducer)
    const red = currentProduct.find(c => c.id === currentUrl)

    useEffect(() => {
        loading()
    })

    function loading() {
        return (
            <div className="d-flex
                            align-items-center 
                            justify-content-center
                            flex-column
                            ">
                <h2>
                    <img width="150px" height="150px" src={pusth} alt="loading" />
                </h2>
                <h3>loading...</h3>

            </div>
        )
    }


    return (
        <div style={{ paddingTop: "150px" }}>
            <Container>
                {
                    red ? (
                        <Row>
                            <Col md={4}>
                                <img src={red.url} alt="img" />
                            </Col>
                            <Col md={6}>
                                <h2>{red.dressName}</h2>
                                <p>
                                    Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
                                </p>
                                {/* <button className="custom-btn" onClick={() => props.feto(detal)} style={{ textDecoration: "none", color: "black" }} href="/">
                                    ADD TO CART +
                                </button> */}
                                <h3>{red.price}$</h3>
                            </Col>
                        </Row>
                    ) : (
                            loading()
                        )
                }
            </Container>
        </div>
    )

}



export default Preview