import React from 'react'
import { Table, UncontrolledPopover, Button } from "reactstrap"
import { useSelector } from "react-redux"

export default function PostList() {

    const showProd = useSelector(state => state.cartReducer)

    return (
        <div>
            <Table striped>
                <thead>
                    <tr style={{ textAlign: "center" }}>
                        <th>#</th>
                        <th>Dress Name</th>
                        <th>Prod-img</th>
                        <th>Price</th>
                        <th>Quantity</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        showProd.map(index =>
                            <tr style={{ textAlign: "center" }} key={index.id + 1}>
                                <th scope="row">{index.id}</th>
                                <td>{index.dressName}</td>
                                <td><div>
                                    <Button
                                        color="primary"
                                        type="button"
                                        id={"PopoverFocus" + index.id}
                                    >
                                        Open img
                                    </Button>
                                    <UncontrolledPopover trigger="focus" placement="right" target={"PopoverFocus" + index.id}>
                                        <img className="w-100" src={index.url} alt="prod-img" />
                                    </UncontrolledPopover>
                                </div></td>
                                <td>{index.price}$</td>
                                <td>{index.quantity}</td>
                            </tr>
                        )
                    }
                </tbody>
            </Table>
        </div>
    )
}
