import React from 'react'
import { useSelector } from "react-redux"
import { Link } from "react-router-dom"
import { Container, Row } from 'reactstrap'
import ProductComp from "./Home/ProductComp"


function Gallery() {

    const allProd = useSelector(state => state.cartReducer)

    return (
        <div>
            <div id="gallery" style={{ paddingTop: "100px" }}>
                <div md={12} className="cart-hero text-center">
                    <h4>All Products</h4>
                    <p><Link to="/react-bucket" >Home</Link><Link to="/cart">Cart</Link></p>
                </div>
            </div>
            <Container>
                <Row className="pt-5 pb-5">
                    {
                        allProd.map(index => <ProductComp key={index.id} index={index} />)
                    }
                </Row>
            </Container>

        </div>
    )
}


export default Gallery