import React, { useState } from 'react';
import { Container, Row, FormGroup, Label, Input, InputGroup, InputGroupAddon, Col, Button } from 'reactstrap';
import { useDispatch, useSelector } from 'react-redux';
import { addDB } from '../../redux/action/userAction';




function AddPost() {

    const selector = useSelector(state => state.cartReducer)

    let newProdId = selector.length

    const [dress, setDress] = useState({
        id: `${newProdId + 1}`,
        quantity: 1
    })

    function handleChange(e) {
        let name = e.target.name;
        let value = e.target.value;

        setDress({
            ...dress,
            [name]: value
        })
    }

    const dispath = useDispatch()

    function handleSubmit(e) {
        console.log(selector)

        e.preventDefault()

        let val = document.getElementsByClassName("prodAdd")[0].value
        let val1 = document.getElementsByClassName("prodAdd")[1].value
        let val3 = document.getElementsByClassName("prodAdd")[2].value


        if (!val && !val1 && !val3) {
            alert("bosluqlari doldurun")
        } else {

            dispath(addDB(dress))

            console.log("Məhsul uğurla əlavə olundu")
        }
    }




    const visiblity = {
        display: "block",
        width: "100%"
    }
    const none = {
        display: "none"
    }

    return (
        <div>
            <Container>
                <h4 style={{ textAlign: "center", marginBottom: "60px" }}>Add Product</h4>
                <Row>
                    <Col md={4}>
                        <form onSubmit={e => handleSubmit(e)}>
                            <FormGroup >
                                <Label for="exampleEmail1">Product Name</Label>
                                <Input className="prodAdd" onChange={e => handleChange(e)} type="text" name="dressName" id="exampleEmail1" />
                            </FormGroup>
                            <FormGroup >
                                <Label for="examplePassword2">Product Image (url)</Label>
                                <Input className="prodAdd" onChange={e => handleChange(e)} type="text" name="url" id="examplePassword2" />
                            </FormGroup>
                            <Label for="examplePassword3">Product Image (url)</Label>
                            <InputGroup>
                                <InputGroupAddon id="examplePassword3" addonType="prepend">$</InputGroupAddon>
                                <Input className="prodAdd" onChange={e => handleChange(e)} type="number" name="price" placeholder="Qimət" min={0} step="1" />
                                <InputGroupAddon addonType="append">.00</InputGroupAddon>
                            </InputGroup>
                            <hr />
                            <Button className="w-100 btn-success">Daxil et</Button>

                        </form>
                    </Col>
                    <Col md={8}>
                        <Row>
                            <Col md={6} xs={12}>
                                <img style={dress.url ? visiblity : none} src={dress.url} alt="prod-img" />
                            </Col>
                            <Col md={6} xs={12}>
                                <div style={{ height: "90px" }}>
                                    <p>Product Name</p>
                                    <h4 style={{ margin: "0px" }}>{dress.dressName}</h4>
                                </div>

                                <div style={{ height: "90px" }}>
                                    <p>Product Price</p>
                                    <h4 style={{ margin: "0px" }}>{dress.price}</h4>
                                </div>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}


export default AddPost;