import React, { useState } from 'react'
import { Container, Col, FormGroup, Label, Input, Row, Button } from 'reactstrap'

function Login() {

    const [reg, setReg] = useState({
        name: "",
        email: "",
        password: ""
    })

    function handleChange(e) {

        let name = e.target.name;
        let value = e.target.value.trim();

        setReg({
            ...reg,
            [name]: value
        })
    }

    function handleSubmit(e) {
        e.preventDefault()

        const mailRe = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if (reg.name.length <= 15 && reg.name.length > 2 && !reg.name.replace(/[\s \d \W+]/g)) {

        }

        if (mailRe.test(reg.email)) {
            console.log(reg)
        }



    }

    return (
        <div style={{ paddingTop: "200px" }}>
            <Container>
                <Row>
                    <Col md={6} xs={12}>
                        <Container>
                            <div style={{ textAlign: "center" }}>
                                <h4 className="text-dark">Login</h4>
                            </div>
                            <FormGroup>
                                <Label for="exampleEmail2">E-mail</Label>
                                <Input type="text" name="name" id="exampleEmail2" />
                            </FormGroup>
                            <FormGroup>
                                <Label for="examplePassword2">Password</Label>
                                <Input type="password" name="password" id="examplePassword2" />
                            </FormGroup>
                            <Button className="w-100" color="primary">Login</Button>
                        </Container>
                    </Col>
                    <Col md={6} xs={12}>
                        <Container>
                            <div style={{ textAlign: "center" }}>
                                <h4 className="text-dark">Sign In</h4>
                            </div>
                            <form onSubmit={e => handleSubmit(e)}>
                                <FormGroup>
                                    <Label for="exampleEmail4">Name</Label>
                                    <Input onChange={e => handleChange(e)} maxLength="15" type="text" name="name" id="exampleEmail4" />
                                </FormGroup>
                                <FormGroup>
                                    <Label for="exampleEmail5">E-mail</Label>
                                    <Input onChange={e => handleChange(e)} type="email" name="email" id="exampleEmail5" />
                                </FormGroup>
                                <FormGroup>
                                    <Label for="exampleEmail6">Password</Label>
                                    <Input onChange={e => handleChange(e)} maxLength="64    " type="password" name="password" id="exampleEmail6" />
                                </FormGroup>
                                <Button className="w-100" color="success">Success</Button>
                            </form>
                        </Container>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default Login